body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

* {
  box-sizing: border-box; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
    /*text-align: center;*/
}

.App-logo {
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #09d3ac;
}
a {
    text-decoration: none;
}
button {
    cursor: pointer;
}

img {
    display: block;
    width: 100%;
    height: auto;
}

.appWrapper {
    max-width: 1200px;
    padding: 15px;
    margin: auto;
    display: grid;

    grid-template-areas: "h h" "n m";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.appWrapperContent {
    padding: 15px;
    grid-area: m;
    background-color: rgba(255, 114, 0, 0.6);
}
.appHeader {
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-area: h;
    padding: 15px;
    background-color: #fff;
    border-bottom: 1px solid rgba(255, 114, 0, 0.6);
}
.appHeader img {
    width: 80px;
    height: auto;
}
.sidebar {
  grid-area: n;
  padding: 15px;
  background-color: rgba(255, 114, 0, 0.6);
  list-style: none;
}
.navList{
  padding: 0;
  margin: 0;
  list-style: none;
}

.navList_item a {
  color: #fff;
}
.navList_item a:hover,
.navList_item a.active{
  color: #666;
}

.userInfo img{
    width: auto;
}

.addPost {
    margin-bottom: 20px;
}
.addPost textarea {
    width: 100%;
    min-height: 80px;

    resize: vertical;
}
.item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    color: #fff;
    text-decoration: none;
}
.userAva {
    flex-shrink: 0;
    width: 60px;
    height: 60px;
    margin-right: 20px;

    border-radius: 50%;
}

.likeCount {
    align-self: flex-end;
    margin-left: auto;
    color: green;
}



.dialogs {
    display: grid;
    grid-template-columns: 3fr 9fr;
}

.dialogsItems {
    padding: 0 20px 0 0;
    border-right: 1px solid #252525;
}
.dialog {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.dialog span {
    overflow: hidden;
    border-radius: 50%;
    margin-right: 10px;
}
.dialog img {
    width: 30px;
    height: 30px;
}

.dialog a{
    color: saddlebrown;
}

.dialog a:hover {
    color: #fff;
}
.dialog a.active {
    color: #fff;
    font-size: 22px;
}

.messages {
    padding: 0 15px;
}

.message {
    margin-bottom: 20px;
    display: flex;
}
.userAva img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 20px;
}
/*.message:nth-child(even) {*/
/*    text-align: right;*/
/*}*/
.messageItem {
    display: inline-block;
    background-color: #fff;
    border-radius: 10px;
    padding: 8px 10px;
}

.sendMessage textarea{
    width: 100%;
    border-radius: 10px;
    border: none;
    resize: none;
    outline: none;
    padding: 10px;
}
.user-item {
    display: flex;
    margin-bottom: 20px;
}
.user-icon_wrap {
    width: 50px;
    height: 50px;
    margin-bottom: 20px;

    border-radius: 50%;
    overflow: hidden;
}
.user-control {
    width: 10%;
}
.user-info {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin-left: 20px;
    padding: 10px 15px;

    background: #fff;
}
